/* styles.css */
.board {
    display: flex;
    background: #ECECEC;
}

.controls {
    display: flex;
    align-items: center;
    margin: 10px;
}

.controls label {
    margin-right: 10px;
}

.controls select {
    padding: 5px;
    font-size: 14px;
}

.column-container {
    display: flex;
    flex-direction: row;
}

.column {
    flex: 1;
    padding: 10px;
}

.card {
    background-color: white;
    border: 1px solid #ddd;
    margin-bottom: 10px;
    padding: 15px;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    gap: 10px;
}

h5 {
    padding-bottom: 5px;
}

h4 {
    margin: 0;
    padding: 0;
    font-size: 18px;
    font-weight: normal;
    color: black;
}

p {
    margin: 5px 0;
}